/* Import SCSS Files */
@import '0-abstracts/variables';
@import '0-abstracts/mixins';
@import '0-abstracts/colors';

@import '1-vendors/animate';

@import '2-vendors-extensions/bootstrap';

@import '3-base/global';
@import '3-base/typography';
@import '3-base/animations';

@import '4-layout/header';
@import '4-layout/footer';

body {
    // height: 3000px;
}

.support-button {
    position: fixed;
    bottom: 2rem;
    left: 2rem;
    z-index: 9999;
    padding-bottom: 0.5rem;
}

.hero-section {
    padding: 6rem 0 8rem;
    background: var(--tt-secondary-color) url(../images/hero-bg.jpg) fixed no-repeat;
    background-size: cover;

    h1 {
        color: #fff;
        margin: 0;
        font-size: 3rem;
    }
}

.login-section {
    position: relative;
    overflow: hidden;
    padding: 5rem 0;

    .pattern-image {
        opacity: 0.1;
        position: absolute;
        bottom: 0;
        left: -15%;
        display: block;
        width: 50%;

        &:last-of-type {
            left: auto;
            right: -30%;
        }
    }

    .nav {
        display: block;
        box-shadow: 0 0 20px rgba(var(--tt-dark-color-rgb), 0.06);
        border-radius: 20px;
    }

    .nav-link {
        width: 100%;
        padding: 2.5rem 2rem;
        text-align: right;
        background-color: var(--tt-white-color);
        border-radius: 0;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:hover {
            // background-color: rgba(var(--tt-primary-color-rgb), 0.07);
            background-color: var(--tt-border-color);
        }

        &::before {
            content: "";
            display: block;
            width: calc(100% - 40px);
            height: 1px;
            background-color: #C0DBDB;
            position: absolute;
            bottom: -0.5px;
            left: 50%;
            transform: translateX(-50%);
        }

        &.active {
            @extend %bg-gradient;

            .logos {
                img:first-of-type {
                    display: none;
                }
                img:last-of-type {
                    display: block;
                }
            }

            .icon-image {
                display: block;
            }
        }

        .logos {
            img {
                display: inline-block;
                width: 120px;
            }
    
            img:last-of-type {
                display: none;
            }
        }

        .icon-image {
            display: none;
        }
    }

    .nav-item {
        &:first-of-type {
            .nav-link {
                border-radius: 20px 20px 0 0;
            }
        }
        &:last-of-type {
            .nav-link {
                border-radius: 0 0 20px 20px;

                &::before {
                    display: none;
                }
            }
        }
    }

    .login-image {
        display: block;
        width: 100%;
        max-width: 370px;

        &--sm {
            max-width: 260px;
        }
    }
}