* {
    box-sizing: border-box;

    :focus {
        outline: none;
    }
    
}

html, body {
    direction: rtl;
}

body {
    background-color: var(--tt-light-color);
    overflow-x: hidden;
}

a, button {
    cursor: pointer;
}

a {
    transition: $transition-base;
}

/* Selection */
::-moz-selection {
    color: #fff;
    background: var(--tt-primary-color);
}

::selection {
    color: #fff;
    background: var(--tt-primary-color);
}

textarea {
    resize: none;
}

.icon-image {
    display: inline-block;
    width: 20px;

    &--lg {
        width: 30px;
    }
}



[class^="tticon-"], [class*=" tticon-"] {
    display: inline-block;
}

.icon-btn {
    font-size: 1.25rem;
    transform: translateY(3px);
    margin-left: 0.1rem;
}

.responsive-video-wrapper {
    aspect-ratio: 16/9;

    iframe {
        display: block;
        width: 100%;
        height: 100%;
    }
}

.separator-h {
    text-align: center;
    display: flex;
    align-items: center;

    span {
        flex-shrink: 0;
    }

    &::before, &::after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: var(--tt-border-color);
        flex-shrink: 1;
    }

    &::before {
        margin-left: 1rem;
    }

    &::after {
        margin-right: 1rem;
    }
}

.attachment {
    display: block;
    width: 100%;
    padding: 0.5rem 1rem;
    background-color: var(--tt-white-color);
    border: 1px solid var(--tt-border-color);
    border-radius: 10px;
    color: var(--tt-dark-color);
    padding-left: 3rem;
    word-wrap: break-word;
    position: relative;
    transition: $transition-base;
    
    &::after {
        content: "\e914";
        font-family: 'tts-icons-font' !important;
        speak: never;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 1rem;
        font-size: 1.25rem;
        color: var(--tt-gray-light-color);
    }

    &:hover {
        background-color: var(--tt-light-color);
        color: var(--tt-dark-color);
    }
}

.attachment-placeholder {
    display: block;
    width: 100%;
    padding: 0.5rem 1rem;
    background-color: var(--tt-white-color);
    border: 1px solid var(--tt-border-color);
    border-radius: 10px;
    color: var(--tt-dark-color);
    word-wrap: break-word;
    position: relative;
    transition: $transition-base;
    display: flex;
    align-items: center;

    &__move-icon {
        cursor: move;
        cursor: grab;
        font-size: 1.25rem;
        margin-left: 0.35rem;
    }

    &__remove-button {
        display: inline-block;
        background: none;
        color: var(--tt-red-color);
        margin-right: auto;
        padding: 0;
        display: flex;
        border: none;
    }

    &__remove-icon {
        font-size: 1.25rem;
    }
}


.or-separator {
    text-align: center;
    position: relative;

    &::before, &::after {
        content: '';
        display: block;
        width: calc(50% - 20px);
        height: 1px;
        background-color: var(--tt-border-color);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
    }

    &::after {
        right: auto;
        left: 0;
    }
}

.circle-icon {
    display: block;
    width: 120px;
    height: 120px;
    background-color: rgba(var(--tt-green-color-rgb), 0.2);
    font-size: 3rem;
    color: var(--tt-green-color);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    &--warning {
        background-color: rgba(var(--tt-yellow-color-rgb), 0.2);
        color: var(--tt-yellow-color);
    }

    &--danger {
        background-color: rgba(var(--tt-red-color-rgb), 0.2);
        color: var(--tt-red-color);
    }
}

.img-circle-fluid {
    display: block;
    width: 80%;
    max-width: 150px;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;

    @include respond-to('md') {
        max-width: 180px;
    }
}

.sortable-handle {
    cursor: move;
    cursor: grab;
}