.navbar {
    padding: 1rem 0;

    .container {
        display: flex;
        flex-wrap: inherit;
        align-items: center;
        justify-content: space-between;
    }

    .navbar-logo {
        display: inline-block;
        max-width: 80px;
    }

    .nav-link {
        color: var(--tt-white-color) !important;
        font-weight: 200;
        transition: $transition-base;

        &:hover {
            color: var(--tt-secondary-color) !important;
        }
    }

    .contact-us {

        .nav-link {
            border-width: 2px;
            transition: $transition-base;

            &:hover {
                color: var(--tt-white-color) !important;
            }
        }
    }
}