h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    line-height: 1.7;
}

body {
    line-height: 1.7;
}

hr {
    background-color: var(--tt-border-color);
    opacity: 1;
}

::placeholder {
    color: var(--tt-gray-light-color) !important;
}

.dropdown {
    &--without-arrow {
        
        .btn::after {
            content: none;
        }
    }
}

.asterisk {
    color: var(--tt-red-color);
}

.form-info-text {
    display: block;
    font-size: $font-size-small;
    color: var(--tt-gray-color);
}

.fs-sm {
    font-size: 0.85rem;
}

.mw-430 {
    max-width: 430px;
    margin-left: auto;
    margin-right: auto;
}

.mw-520 {
    max-width: 520px;
    margin-left: auto;
    margin-right: auto;
}

.mw-150 {
    display: block;
    width: 100%;
    max-width: 150px;
}

.mw-180 {
    max-width: 180px;
    margin-left: auto;
    margin-right: auto;
}

.mw-220 {
    max-width: 220px;
    margin-left: auto;
    margin-right: auto;
}

.mw-800 {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.mw-970 {
    max-width: 970px;
    margin-left: auto;
    margin-right: auto;
}

.form-control-lg {
    border-radius: 10px;
    font-size: 1.1rem
}

.form-select-lg {
    border-radius: 10px;
    font-size: 1.1rem
}


.form-label {
    color: var(--tt-dark-color);
    font-weight: 500;
    display: block
}

.show-password-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0.6rem;
    background: none;
    color: var(--tt-gray-light-color);
    border: none;
    font-size: 1.25rem;
    transition: $transition-base;

    &:hover {
        color: var(--tt-dark-color);
    }

    span {
        display: block;
    }

&.toggle {
        span::before {
            content: "\e93e";
        }
    }
}

.btn {
    color: var(--tt-dark-color);
    user-select: none;
    background-color: rgba(0,0,0,0);
    border: 1px solid rgba(0,0,0,0);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    border-radius: 10px;
    -webkit-appearance: none;
    transition: $transition-base;

    &:hover, &:active {
        color: var(--tt-dark-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-dark-color-rgb), 0.25) !important;
    }
}

.btn-gradient-primary {
    @extend %bg-gradient-with-hover;
    color: #fff;
    border: none !important;
    padding-top: 0.445rem;
    padding-bottom: 0.445rem;

    &:hover {
        color: #fff;
    }

    &.btn-lg {
        padding-top: 0.66rem;
        padding-bottom: 0.66rem;
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25) !important;
    }
}

.btn-primary {
    background-color: var(--tt-primary-color);
    border-color: var(--tt-primary-color);

    &:hover {
        background-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) - 4%));
        border-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) - 4%));
    }
}

.btn-secondary {
    background-color: var(--tt-secondary-color);
    border-color: var(--tt-secondary-color);
    color: var(--tt-white-color);

    &:hover {
        color: var(--tt-white-color);
        background-color: hsl(var(--tt-secondary-color-h), var(--tt-secondary-color-s), calc(var(--tt-secondary-color-l) - 4%));
        border-color: hsl(var(--tt-secondary-color-h), var(--tt-secondary-color-s), calc(var(--tt-secondary-color-l) - 4%));
    }

    &:focus {
        color: var(--tt-white-color);
        background-color: var(--tt-secondary-color);
        border-color: var(--tt-secondary-color);
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-secondary-color-rgb), 0.25) !important;
    }
}

.btn-outline-primary {
    color: var(--tt-primary-color);
    border-color: var(--tt-primary-color);

    &:hover {
        background-color: var(--tt-primary-color);
        border-color: var(--tt-primary-color);
        color: var(--tt-white-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25) !important;
    }
}

.btn-outline-secondary {
    color: var(--tt-secondary-color);
    border-color: var(--tt-secondary-color);

    &:hover {
        background-color: var(--tt-secondary-color);
        border-color: var(--tt-secondary-color);
        color: var(--tt-white-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-secondary-color-rgb), 0.25) !important;
    }
}

.btn-outline-danger {
    border-color: var(--tt-red-color);
    color: var(--tt-red-color);

    &:hover {
        color: #fff;
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-red-color-rgb), 0.25) !important;
    }
}

.btn-outline-success {
    border-color: var(--tt-green-color);
    color: var(--tt-green-color);

    &:hover {
        color: #fff;
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-green-color-rgb), 0.25) !important;
    }
}


.btn-light {
    background-color: var(--tt-white-color);
    color: var(--tt-dark-color);

    &:hover {
        background-color: hsl(var(--tt-white-color-h), var(--tt-white-color-s), calc(var(--tt-white-color-l) + 4%));
        border-color: hsl(var(--tt-white-color-h), var(--tt-white-color-s), calc(var(--tt-white-color-l) + 4%));
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-white-color-rgb), .25) !important;
    }
    
}

.btn-dark {
    background-color: var(--tt-dark-color);
    border-color: var(--tt-dark-color);
    color: var(--tt-white-color);

    &:hover {
        color: var(--tt-white-color);
        background-color: hsl(var(--tt-dark-color-h), var(--tt-dark-color-s), calc(var(--tt-dark-color-l) + 6%));
        border-color: hsl(var(--tt-dark-color-h), var(--tt-dark-color-s), calc(var(--tt-dark-color-l) + 6%));
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-dark-color-rgb), .25) !important;
    }

}

.btn-outline-gray {
    color: var(--tt-gray-color);
    border-color: var(--tt-gray-color);

    &:hover {
        color: var(--tt-dark-color);
        border-color: var(--tt-dark-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-dark-color-rgb), .25) !important;
    }
}

.btn-lg {
    padding: .6rem 1rem;
    border-radius: 10px;
    font-size: 1.15rem;
}

.btn-xl {
    padding: .9rem 2.25rem;
    border-radius: 10px;
    font-size: 1.15rem;
    font-weight: 500;
}

.btn-wide {
    padding-left: 2.25rem;
    padding-right: 2.25rem;
}

.table>:not(caption)>*>* {
    box-shadow: none;
}

.table-striped>tbody>tr:nth-of-type(odd)>* {
    background-color: var(--tt-light-color);
}

.table-striped>tbody>tr:nth-of-type(even)>* {
    background-color: var(--tt-white-color);
}

.shadow-lg {
    box-shadow: 0 0 3rem rgba(#000, .175) !important;
}

.accordion {
    max-width: 870px;
    margin-left: auto;
    margin-right: auto;


}

.accordion-flush {

    .accordion-item {
        margin-bottom: 5px;
        border-radius: 10px;
        overflow: hidden;
        border: 1px solid var(--tt-border-color) !important;
    }

    .accordion-button {
        font-weight: bold;
        border-radius: 10px;

        &:not(.collapsed) {
            box-shadow: none;
        }
    }

}

.accordion-item:last-of-type .accordion-button.collapsed {
    border-radius: 0 !important;
}

.accordion-item {
    background-color: var(--tt-white-color);
    border: 1px solid var(--tt-border-color);
}

.accordion-button {
    background-color: var(--tt-white-color);
    color: var(--tt-dark-color);
}

.text-gray {
    color: var(--tt-gray-color) !important;
}

.link-gray {
    color: var(--tt-gray-color) !important;

    &:hover {
        color: var(--tt-primary-color) !important;
    }
}

.bg-gradient {
    @extend %bg-gradient;
}

.breadcrumb {
    a {
        color: var(--tt-gray-color);
        text-decoration: none;
        transition: $transition-base;

        &:hover {
            color: var(--tt-primary-color);
        }
    }

    .breadcrumb-item.active {
        color: var(--tt-gray-light-color);
    }
}

.img-responsive {
    display: block;
    width: 100%;
    max-width: 100%;
}

.tooltip-inner {
    font-family: 'Somar', sans-serif;
    padding-top: 0.25rem;
    padding-bottom: 0.4rem;
    font-size: 0.75rem;
    background-color: var(--tt-dark-color);
    color: var(--tt-white-color);
}

.tooltip.bs-tooltip-right .tooltip-arrow::before {
    border-right-color: var(--tt-dark-color) !important;
}
.tooltip.bs-tooltip-left .tooltip-arrow::before {
    border-left-color: var(--tt-dark-color) !important;
}
.tooltip.bs-tooltip-bottom .tooltip-arrow::before {
    border-bottom-color: var(--tt-dark-color) !important;
}
.tooltip.bs-tooltip-top .tooltip-arrow::before {
    border-top-color: var(--tt-dark-color) !important;
}


  

.row-gutter-5 {
    --bs-gutter-x: 5px;
}

.alert {

    &--with-remove-button {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .alert-remove {
        font-size: 1.5rem;
        margin-right: 1rem;
        color: inherit;
        text-decoration: none;

        span {
            display: block;
        }
    }
}

.nav-pills {

    &--with-arrow {

        .nav-link {
            position: relative;
            transition: none;

            &::before {
                content: '';
                display: block;
                position: absolute;
                bottom: -8px;
                left: 50%;
                border-left: 8px solid transparent;
                border-right: 8px solid transparent;
                border-top: 8px solid var(--tt-primary-color);
                transform: translateX(-50%);
                display: none;
            }

            &.active {
                
                &::before {
                    display: block;
                }
            }
        }
    }
}

.border, .border-start {
    border-color: var(--tt-border-color) !important;
}

.border-dashed {
    border-style: dashed !important;
}

.border-gray-light {
    border-color: var(--tt-gray-light-color) !important;
}

/************
*************
*************

Start Bootstap Colors Overwrite

*************
*************
************/

a {
    color: var(--tt-primary-color);

    &:hover {
        color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) - 10%))
    }
}

.text-primary {
    color: var(--tt-primary-color) !important;
}

.text-muted {
    color: var(--tt-gray-light-color) !important;
}

.accordion-button:not(.collapsed) {
    color: var(--tt-dark-color);
    background-color: var(--tt-white-color);

    &::after {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
    }
}

.accordion-button:focus {
    border-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) + 8%));
    box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25);
}

.form-check-input {
    -webkit-appearance:none;

    &:focus {
        border-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) + 8%)) !important;
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25);
    }
}

.form-check-input:checked {
    background-color: var(--tt-primary-color);
    border-color: var(--tt-primary-color) !important;
}

.pagination {

    .page-item {

        .page-link {
            color: var(--tt-primary-color);
            background-color: var(--tt-white-color);
            border-color: var(--tt-border-color);

            &:hover {
                background-color: var(--tt-primary-color);
                border-color: var(--tt-primary-color);
                color: #fff !important;
            }
    
            &:focus {
                background-color: var(--tt-primary-color);
                box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25);
                color: #fff !important;
                border-color: var(--tt-primary-color);
            }
        }

        &.active {
            .page-link {
                background-color: var(--tt-primary-color);
                border-color: var(--tt-primary-color) !important;
                color: #fff;
            }
        }

        &.disabled {
            .page-link {
                color: var(--tt-gray-light-color) !important;
            }
        }

    }
}

.bg-primary {
    background-color: var(--tt-primary-color) !important;

    ::selection {
        background-color: var(--tt-dark-color);
        color: var(--tt-white-color);
    }
    
}

.bg-white {
    background-color: var(--tt-white-color) !important;
}

.bg-dark {
    background-color: var(--tt-dark-color) !important;
}

.text-dark {
    color: var(--tt-dark-color) !important;
}

.text-white {
    color: var(--tt-white-color) !important;
}

.btn-outline-dark {
    color: var(--tt-dark-color);
    border-color: var(--tt-dark-color);

    &:hover {
        background-color: var(--tt-dark-color);
        color: var(--tt-white-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-dark-color-rgb), 0.25) !important;
        border-color: var(--tt-dark-color);
    }
}

.btn-outline-light {
    color: var(--tt-white-color);
    border-color: var(--tt-white-color);

    &:hover {
        background-color: var(--tt-white-color);
        color: var(--tt-dark-color);
    }

    &:focus {
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-white-color-rgb), 0.25) !important;
        border-color: var(--tt-white-color);
    }
}

.dropdown-menu {
    background-color: var(--tt-white-color);
    color: var(--tt-gray-color);
    border-color: var(--tt-border-color);
}

.dropdown-item {
    color: var(--tt-gray-color);

    &:hover, &:focus {
        background-color: rgba(var(--tt-dark-color-rgb), 0.06);
        color: var(--tt-dark-color);
    }
}

.dropdown-divider {
    background: none;
    border-color: var(--tt-border-color);
}

.form-control, .form-select {
    background-color: var(--tt-white-color);
    border-color: #cbcbcb;
    color: var(--tt-dark-color) !important;
    -webkit-appearance:none;

    &:disabled {
        background-color: rgba(var(--tt-dark-color-rgb), 0.06);
    }

    &:focus {
        border-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) + 8%)) !important;
        box-shadow: 0 0 0 0.25rem rgba(var(--tt-primary-color-rgb), 0.25);
        background-color: var(--tt-white-color);
    }
}

.form-check-input {
    background-color: var(--tt-white-color);
    border-color: #cbcbcb;
    color: var(--tt-dark-color) !important;
}

.modal-md {
    max-width: 650px;
}

.modal-content {
    background-color: var(--tt-white-color);
}

.modal-header {
    border-bottom-color: var(--tt-border-color);
}

.modal-footer {
    border-color: var(--tt-border-color);
}

.modal-backdrop {
    opacity: 0.7 !important;
}

.table {
    color: var(--tt-dark-color);
}

.table>:not(caption)>*>* {
    border-color: var(--tt-border-color);
}

.table-striped>tbody>tr:nth-of-type(odd)>* {
    --bs-table-accent-bg: var(--tt-white-color);
}

.border, .border-top, .border-left, .border-right, .border-bottom {
    border-color: var(--tt-border-color) !important;
}

.bg-light {
    background-color: var(--tt-light-color) !important;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    background-color: var(--tt-primary-color);
}

.nav-link {
    color: var(--tt-primary-color);

    &:hover {
        color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) - 20%));
    }
}

.btn-close:focus {
    box-shadow: 0 0 0 0.25rem rgba(var(--tt-dark-color-rgb), 0.25);
}

