.footer {

    a {
        color: var(--tt-white-color);
        text-decoration: none;
        font-weight: 200;

        &:hover {
            color: var(--tt-secondary-color);
        }
    }

    p {
        color: var(--tt-white-color);
        font-weight: 200;
    }

    &__top-area {
        padding: 1.5rem 0;
        background-color: hsl(var(--tt-primary-color-h), var(--tt-primary-color-s), calc(var(--tt-primary-color-l) - 4%));
    }

    &__bottom-area {
        padding: 2.5rem 0;
        background-color: var(--tt-primary-color);

        .footer-logo-translate {
            transform: translateY(-7px);
        }
    }

    .social-media {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        align-items: center;

        li {
            margin-left: 1rem;

            &:last-of-type {
                margin-left: 0;
            }
        }

        a {
            display: block;
        }

        img {
            display: block;
            width: 25px;
        }
    }

    &__to-top-button {
        width: 50px;
        height: 50px;
        padding: 0;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            display: block;
            width: 25px;
        }
    }
}