.heartbeat-animation {
    animation: heartbeat 2s infinite;
}

@keyframes heartbeat {
    0% {
        transform: scale(1) translateY(2px);
    }
    5% {
        transform: scale(1.3) translateY(2px);
    }
    10% {
        transform: scale(1.1) translateY(2px);
    }
    15% {
        transform: scale(1.5) translateY(2px);
    }
    50% {
        transform: scale(1) translateY(2px);
    }
    100% {
        transform: scale(1) translateY(2px);
    }
}

@keyframes flashing {
    0% {
        opacity: 0.5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
    100% {
        opacity: 0;
        top: -15px;
        left: -15px;
        right: -15px;
        bottom: -15px;
    }
}